	module.exports = {"pathContext":{"docHtml":"<h1>2.12.2 / 2017-11-28</h1>\n<ul>\n<li>fix: typings are not shipped</li>\n</ul>\n<h1>2.12.1 / 2017-11-23</h1>\n<ul>\n<li>Move @types/node to dev dependency</li>\n</ul>\n<h1>2.12.0 / 2017-11-22</h1>\n<ul>\n<li>add attributeName() method to Option objects</li>\n<li>Documentation updated for options with --no prefix</li>\n<li>typings: <code>outputHelp</code> takes a string as the first parameter</li>\n<li>typings: use overloads</li>\n<li>feat(typings): update to match js api</li>\n<li>Print default value in option help</li>\n<li>Fix translation error</li>\n<li>Fail when using same command and alias (#491)</li>\n<li>feat(typings): add help callback</li>\n<li>fix bug when description is add after command with options (#662)</li>\n<li>Format js code</li>\n<li>Rename History.md to CHANGELOG.md (#668)</li>\n<li>feat(typings): add typings to support TypeScript (#646)</li>\n<li>use current node</li>\n</ul>\n<h1>2.11.0 / 2017-07-03</h1>\n<ul>\n<li>Fix help section order and padding (#652)</li>\n<li>feature: support for signals to subcommands (#632)</li>\n<li>Fixed #37, --help should not display first (#447)</li>\n<li>Fix translation errors. (#570)</li>\n<li>Add package-lock.json</li>\n<li>Remove engines</li>\n<li>Upgrade package version</li>\n<li>Prefix events to prevent conflicts between commands and options (#494)</li>\n<li>Removing dependency on graceful-readlink</li>\n<li>Support setting name in #name function and make it chainable</li>\n<li>Add .vscode directory to .gitignore (Visual Studio Code metadata)</li>\n<li>Updated link to ruby commander in readme files</li>\n</ul>\n<h1>2.10.0 / 2017-06-19</h1>\n<ul>\n<li>Update .travis.yml. drop support for older node.js versions.</li>\n<li>Fix require arguments in README.md</li>\n<li>On SemVer you do not start from 0.0.1</li>\n<li>Add missing semi colon in readme</li>\n<li>Add save param to npm install</li>\n<li>node v6 travis test</li>\n<li>Update Readme_zh-CN.md</li>\n<li>Allow literal '--' to be passed-through as an argument</li>\n<li>Test subcommand alias help</li>\n<li>link build badge to master branch</li>\n<li>Support the alias of Git style sub-command</li>\n<li>added keyword commander for better search result on npm</li>\n<li>Fix Sub-Subcommands</li>\n<li>test node.js stable</li>\n<li>Fixes TypeError when a command has an option called <code>--description</code></li>\n<li>Update README.md to make it beginner friendly and elaborate on the difference between angled and square brackets.</li>\n<li>Add chinese Readme file</li>\n</ul>\n<h1>2.9.0 / 2015-10-13</h1>\n<ul>\n<li>Add option <code>isDefault</code> to set default subcommand #415 @Qix-</li>\n<li>Add callback to allow filtering or post-processing of help text #434 @djulien</li>\n<li>Fix <code>undefined</code> text in help information close #414 #416 @zhiyelee</li>\n</ul>\n<h1>2.8.1 / 2015-04-22</h1>\n<ul>\n<li>Back out <code>support multiline description</code> Close #396 #397</li>\n</ul>\n<h1>2.8.0 / 2015-04-07</h1>\n<ul>\n<li>Add <code>process.execArg</code> support, execution args like <code>--harmony</code> will be passed to sub-commands #387 @DigitalIO @zhiyelee</li>\n<li>Fix bug in Git-style sub-commands #372 @zhiyelee</li>\n<li>Allow commands to be hidden from help #383 @tonylukasavage</li>\n<li>When git-style sub-commands are in use, yet none are called, display help #382 @claylo</li>\n<li>Add ability to specify arguments syntax for top-level command #258 @rrthomas</li>\n<li>Support multiline descriptions #208 @zxqfox</li>\n</ul>\n<h1>2.7.1 / 2015-03-11</h1>\n<ul>\n<li>Revert #347 (fix collisions when option and first arg have same name) which causes a bug in #367.</li>\n</ul>\n<h1>2.7.0 / 2015-03-09</h1>\n<ul>\n<li>Fix git-style bug when installed globally. Close #335 #349 @zhiyelee</li>\n<li>Fix collisions when option and first arg have same name. Close #346 #347 @tonylukasavage</li>\n<li>Add support for camelCase on <code>opts()</code>. Close #353  @nkzawa</li>\n<li>Add node.js 0.12 and io.js to travis.yml</li>\n<li>Allow RegEx options. #337 @palanik</li>\n<li>Fixes exit code when sub-command failing.  Close #260 #332 @pirelenito</li>\n<li>git-style <code>bin</code> files in $PATH make sense. Close #196 #327  @zhiyelee</li>\n</ul>\n<h1>2.6.0 / 2014-12-30</h1>\n<ul>\n<li>added <code>Command#allowUnknownOption</code> method. Close #138 #318 @doozr @zhiyelee</li>\n<li>Add application description to the help msg. Close #112 @dalssoft</li>\n</ul>\n<h1>2.5.1 / 2014-12-15</h1>\n<ul>\n<li>fixed two bugs incurred by variadic arguments. Close #291 @Quentin01 #302 @zhiyelee</li>\n</ul>\n<h1>2.5.0 / 2014-10-24</h1>\n<ul>\n<li>add support for variadic arguments. Closes #277 @whitlockjc</li>\n</ul>\n<h1>2.4.0 / 2014-10-17</h1>\n<ul>\n<li>fixed a bug on executing the coercion function of subcommands option. Closes #270</li>\n<li>added <code>Command.prototype.name</code> to retrieve command name. Closes #264 #266 @tonylukasavage</li>\n<li>added <code>Command.prototype.opts</code> to retrieve all the options as a simple object of key-value pairs. Closes #262 @tonylukasavage</li>\n<li>fixed a bug on subcommand name. Closes #248 @jonathandelgado</li>\n<li>fixed function normalize doesnâ€™t honor option terminator. Closes #216 @abbr</li>\n</ul>\n<h1>2.3.0 / 2014-07-16</h1>\n<ul>\n<li>add command alias'. Closes PR #210</li>\n<li>fix: Typos. Closes #99</li>\n<li>fix: Unused fs module. Closes #217</li>\n</ul>\n<h1>2.2.0 / 2014-03-29</h1>\n<ul>\n<li>add passing of previous option value</li>\n<li>fix: support subcommands on windows. Closes #142</li>\n<li>Now the defaultValue passed as the second argument of the coercion function.</li>\n</ul>\n<h1>2.1.0 / 2013-11-21</h1>\n<ul>\n<li>add: allow cflag style option params, unit test, fixes #174</li>\n</ul>\n<h1>2.0.0 / 2013-07-18</h1>\n<ul>\n<li>remove input methods (.prompt, .confirm, etc)</li>\n</ul>\n<h1>1.3.2 / 2013-07-18</h1>\n<ul>\n<li>add support for sub-commands to co-exist with the original command</li>\n</ul>\n<h1>1.3.1 / 2013-07-18</h1>\n<ul>\n<li>add quick .runningCommand hack so you can opt-out of other logic when running a sub command</li>\n</ul>\n<h1>1.3.0 / 2013-07-09</h1>\n<ul>\n<li>add EACCES error handling</li>\n<li>fix sub-command --help</li>\n</ul>\n<h1>1.2.0 / 2013-06-13</h1>\n<ul>\n<li>allow \"-\" hyphen as an option argument</li>\n<li>support for RegExp coercion</li>\n</ul>\n<h1>1.1.1 / 2012-11-20</h1>\n<ul>\n<li>add more sub-command padding</li>\n<li>fix .usage() when args are present. Closes #106</li>\n</ul>\n<h1>1.1.0 / 2012-11-16</h1>\n<ul>\n<li>add git-style executable subcommand support. Closes #94</li>\n</ul>\n<h1>1.0.5 / 2012-10-09</h1>\n<ul>\n<li>fix <code>--name</code> clobbering. Closes #92</li>\n<li>fix examples/help. Closes #89</li>\n</ul>\n<h1>1.0.4 / 2012-09-03</h1>\n<ul>\n<li>add <code>outputHelp()</code> method.</li>\n</ul>\n<h1>1.0.3 / 2012-08-30</h1>\n<ul>\n<li>remove invalid .version() defaulting</li>\n</ul>\n<h1>1.0.2 / 2012-08-24</h1>\n<ul>\n<li>add <code>--foo=bar</code> support [arv]</li>\n<li>fix password on node 0.8.8. Make backward compatible with 0.6 [focusaurus]</li>\n</ul>\n<h1>1.0.1 / 2012-08-03</h1>\n<ul>\n<li>fix issue #56</li>\n<li>fix tty.setRawMode(mode) was moved to tty.ReadStream#setRawMode() (i.e. process.stdin.setRawMode())</li>\n</ul>\n<h1>1.0.0 / 2012-07-05</h1>\n<ul>\n<li>add support for optional option descriptions</li>\n<li>add defaulting of <code>.version()</code> to package.json's version</li>\n</ul>\n<h1>0.6.1 / 2012-06-01</h1>\n<ul>\n<li>Added: append (yes or no) on confirmation</li>\n<li>Added: allow node.js v0.7.x</li>\n</ul>\n<h1>0.6.0 / 2012-04-10</h1>\n<ul>\n<li>Added <code>.prompt(obj, callback)</code> support. Closes #49</li>\n<li>Added default support to .choose(). Closes #41</li>\n<li>Fixed the choice example</li>\n</ul>\n<h1>0.5.1 / 2011-12-20</h1>\n<ul>\n<li>Fixed <code>password()</code> for recent nodes. Closes #36</li>\n</ul>\n<h1>0.5.0 / 2011-12-04</h1>\n<ul>\n<li>Added sub-command option support [itay]</li>\n</ul>\n<h1>0.4.3 / 2011-12-04</h1>\n<ul>\n<li>Fixed custom help ordering. Closes #32</li>\n</ul>\n<h1>0.4.2 / 2011-11-24</h1>\n<ul>\n<li>Added travis support</li>\n<li>Fixed: line-buffered input automatically trimmed. Closes #31</li>\n</ul>\n<h1>0.4.1 / 2011-11-18</h1>\n<ul>\n<li>Removed listening for \"close\" on --help</li>\n</ul>\n<h1>0.4.0 / 2011-11-15</h1>\n<ul>\n<li>Added support for <code>--</code>. Closes #24</li>\n</ul>\n<h1>0.3.3 / 2011-11-14</h1>\n<ul>\n<li>Fixed: wait for close event when writing help info [Jerry Hamlet]</li>\n</ul>\n<h1>0.3.2 / 2011-11-01</h1>\n<ul>\n<li>Fixed long flag definitions with values [felixge]</li>\n</ul>\n<h1>0.3.1 / 2011-10-31</h1>\n<ul>\n<li>Changed <code>--version</code> short flag to <code>-V</code> from <code>-v</code></li>\n<li>Changed <code>.version()</code> so it's configurable [felixge]</li>\n</ul>\n<h1>0.3.0 / 2011-10-31</h1>\n<ul>\n<li>Added support for long flags only. Closes #18</li>\n</ul>\n<h1>0.2.1 / 2011-10-24</h1>\n<ul>\n<li>\"node\": \">= 0.4.x &#x3C; 0.7.0\". Closes #20</li>\n</ul>\n<h1>0.2.0 / 2011-09-26</h1>\n<ul>\n<li>Allow for defaults that are not just boolean. Default peassignment only occurs for --no-*, optional, and required arguments. [Jim Isaacs]</li>\n</ul>\n<h1>0.1.0 / 2011-08-24</h1>\n<ul>\n<li>Added support for custom <code>--help</code> output</li>\n</ul>\n<h1>0.0.5 / 2011-08-18</h1>\n<ul>\n<li>Changed: when the user enters nothing prompt for password again</li>\n<li>Fixed issue with passwords beginning with numbers [NuckChorris]</li>\n</ul>\n<h1>0.0.4 / 2011-08-15</h1>\n<ul>\n<li>Fixed <code>Commander#args</code></li>\n</ul>\n<h1>0.0.3 / 2011-08-15</h1>\n<ul>\n<li>Added default option value support</li>\n</ul>\n<h1>0.0.2 / 2011-08-15</h1>\n<ul>\n<li>Added mask support to <code>Command#password(str[, mask], fn)</code></li>\n<li>Added <code>Command#password(str, fn)</code></li>\n</ul>\n<h1>0.0.1 / 2010-01-03</h1>\n<ul>\n<li>Initial release</li>\n</ul>","docPath":"argo-ci/node_modules/tslint/node_modules/commander/changelog","proj":"argo-ci"}}

